@import url(https://fonts.googleapis.com/css2?family=Bungee&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Baloo+Tamma+2:wght@500&display=swap);

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  background: #ccc;
}
.header {
  margin: 4rem;
  text-transform: uppercase; 
  font-family: 'Baloo Tamma 2', sans-serif;
  font-size: 2rem;
}


.image1 {
  position: absolute;
  left: 0;
  bottom: 0;

}
.image2 {
  position: absolute;
  top: 0;
  right: 0;
  width: 400px;

}
.image3 {
  position: absolute;
  right: 0;
  bottom: 0;
}

.image4{
   position: absolute;
   right: 0;
   bottom: 0;
   width: 140px;
}
.image5 {
  position: absolute;
  right: 0;
  left: 0;

}

.FoodList {
    display: flex;
    align-items: center; 
    justify-content: space-between;
    flex-wrap: wrap;
    border-radius: 10px; 
    width: 80%;
    background: white;
}
.FoodDetail {
    width: 22%;
    height: 350px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    border-radius: 10px;
    box-shadow: 0 2px 2px 1px #ccc;  
    padding: .8rem; 
    margin: .4rem;
    font-family: 'Baloo Tamma 2', sans-serif;

} 


.FoodDetail .image {
    width: 100%;
    height: 90%;
    background-size: cover;
    background-position: center;
    margin-bottom: 5px;
    border-radius: 10px;
}


.details {
    width: 100%;
    height: 30%;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center; 
    box-shadow: 0px -26px 10px 0px #fff;
}

.details h1 {
    font-size: 1rem;
}
.price-label {
    width: 100%;
    height: 5%;
    display: flex;
    justify-content: space-between;
    margin-bottom: .6rem;
}

.details p {
    font-size: .8rem;
}
.search {
    border: 1px solid #fff;
    border-radius: 5px;
    height: 20px;
    width: 100%;
    padding: 1rem 4rem;
    margin: 1rem;
    outline: 0;
    background-color: #f5f5f5;
    text-transform: uppercase;
    text-align: center;
  }
